.container {
  width: 100%;
  height: 100%;

//min-width: 100px;
//  min-height: 100px;
//
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;


  padding: 5px;
  flex-wrap: wrap;
  font-size: 18px;

  border-radius: 10px;
  text-align: center;
  color: black;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);
  aspect-ratio: 1 / 1;
  &:hover {
    cursor: pointer;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.25);
  }
}

.product {
  font-size:1em;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  //font-size: 20px;

  //background-color: teal;
  height: 40%;
  word-wrap: break-word;
  white-space: normal;
}
.brand {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d77654;
  //background-color: chocolate;
  width: 100%;
  height: 20%;
  font-size: 1em;

  word-wrap: break-word;
  white-space: normal;
}

.price {
  display: flex;
  justify-content: center;
  align-items: center;
  //background-color: red;
  width: 100%;
  height: 20%;
  word-wrap: break-word;
  white-space: normal;
  font-size: 1em;
  .span {
    color: green;
    font-size: 1em;
    padding-left: 3px;
    padding-right: 3px;
  }
}


.id {
  //word-wrap: break-word;
  //white-space: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  //background-color: cornflowerblue;
  width: 100%;
  height: 20%;
  font-size: 0.5em;


}

