@media screen and (max-width: 879px) {
  .container {
    font-size: 22px;
  }
}

@media screen and (max-width: 659px) {
  .container {
    font-size: 24px;
  }
}

@media screen and (max-width: 450px) {
  .container {
    font-size: 16px;
  }
}

@media screen and (max-width: 339px) {
  .container {
    font-size: 12px;
  }
}
@media screen and (max-width: 239px) {
  .container {
    font-size: 22px;
  }
}
